	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Wrong Passport","date":"07 Feb 2007","slug":"/2007-02-07-wrong-passport/","tags":"Y2007"},"html":"<p>From a <a href=\"http://www.dw-world.de/dw/article/0,,2338613,00.html\">Deutsche Welle analysis of the Kurnaz case</a>:</p>\n<blockquote><p>\n  Legally, Berlin was under no obligation to protect a German-born Turkish citizen imprisoned in Guantanamo. But moral and political responsibility for a permanent resident unjustly detained is another matter, experts say.\n</p></blockquote>\n<p>Um, right, like the German government is going to lift a finger for <em>me</em> if I ever get in trouble. Let's just say that if I've got one phone call before I get shipped to the Gulag, I'm calling the embassy with the blue passports, not the one with the red passports.</p>"},"next":{"fields":{"title":"Conscientious Objector","date":"07 Feb 2007","slug":"/2007-02-07-conscientious-objector/"}},"previous":{"fields":{"title":"Hello World, We're Here Again","date":"09 Feb 2007","slug":"/2007-02-09-hello-world-were-here-again/"}}},{"node":{"fields":{"title":"Conscientious Objector","date":"07 Feb 2007","slug":"/2007-02-07-conscientious-objector/","tags":"Y2007"},"html":"<p>In case you wonder why developers are so difficult to work with, <a href=\"http://lumma.de/eintrag.php?id=3248\">Nico heard this</a> in our office in Cologne:</p>\n<blockquote><p>\n  I reject <a href=\"http://www.rubyonrails.org/\">Ruby on Rails</a> for reasons of conscience.\n</p></blockquote>\n<p>Try explaining that to a client when your project is late and over budget. \"We could have used a better framework, but our development team thought it was immoral.\"</p>"},"next":{"fields":{"title":"Attitude","date":"06 Feb 2007","slug":"/2007-02-06-attitude/"}},"previous":{"fields":{"title":"Wrong Passport","date":"07 Feb 2007","slug":"/2007-02-07-wrong-passport/"}}},{"node":{"fields":{"title":"Attitude","date":"06 Feb 2007","slug":"/2007-02-06-attitude/","tags":"Y2007"},"html":"<p>After<a href=\"http://www.papascott.de/archives/2007/02/05/traction-times-ten/\"> pissing and moaning yesterday</a> about my back, I slept through the night last night and feel pretty good today. Letting off steam helps (as does a pillow for the legs and new exercises).</p>\n<p>Oh, and welcome to the new <a href=\"http://cutline.tubetorial.com/\">look</a>.</p>"},"next":{"fields":{"title":"Traction Times Ten","date":"05 Feb 2007","slug":"/2007-02-05-traction-times-ten/"}},"previous":{"fields":{"title":"Conscientious Objector","date":"07 Feb 2007","slug":"/2007-02-07-conscientious-objector/"}}}],"pathPrefix":"","first":false,"last":false,"index":157,"pageCount":1101}}

